import axios from 'axios';

const backend = 'http://localhost:3000';
/* location.href
	.split('/')
	.slice(0, 3)
	.join('/'); */

const get = async url => {
	try {
		const response = await axios.get(`${backend}${url}`, {
			withCredentials: true,
		});

		console.log(`GET ${url} -> (${response.status})`, response.data);

		if (response.status === 200) {
			return response.data;
		}
		throw response.data;
	} catch (e) {
		console.error(`GET ${url} -> `, e);
		throw e;
	}
};

const post = async (url, body) => {
	try {
		const response = await axios.post(`${backend}${url}`, body, {
			withCredentials: true,
		});

		console.log(
			`POST ${url} (${JSON.stringify(body)}) -> (${response.status})`,
			response.data
		);

		if (response.status === 200) {
			return response.data;
		}
		throw response.data;
	} catch (e) {
		console.log(`POST ${url} (${JSON.stringify(body)}) -> `, e);
		throw e;
	}
};

// users
export const login = (username, password) =>
	post(`/api/auth/login`, { username, password });

export const register = (username, password) =>
	post(`/api/auth/register`, { username, password });

export const isUserExist = login => get(`/api/auth/user-exist/${login}`);

// words
export const searchWord = async q => get(`/api/words?q=${q}`);

// courses managing
export const getUserCourses = async () => get('/api/course');
export const getCourseById = async (id, authorId) =>
	get(`/api/course/${authorId}/${id}`);
export const createCourse = async data => post('/api/course', data);
export const getCourse = async () => {};
export const getRandomSentences = async ids =>
	get(`/api/sentences/randomByWord/${ids.join(',')}`);

